import React from "react"
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";

import headerBackground from 'assets/images/banner2.jpg';

export default class Jewellery extends React.Component {
    render() {
        return (
            <div className='homepage center center-page-x'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>3d and AR apps for online jewellery retail</title>
                    <link rel="canonical" href="https://www.ezyvue.com/industries/jewellery" />
                </Helmet>
                <section className="full-width-header"
                    style={{
                        backgroundImage: `url(${headerBackground})`,
                        justifyContent: 'right',
                        backgroundPosition: '0%'
                    }
                    }>
                    <div style={{
                        maxWidth: '600px',
                    }}>
                        <h1>Realistic web-based 3D visualization for online jewelry retailers and manufacturers</h1>
                        <h3>Bring a unique viewing experience to your online store with the 3D Viewer</h3>
                    </div>
                    <div className='full-width-header-promo-button'>
                        <div className="homepage-button-medium">
                            <Link to='/pricing'>Get started for free</Link>
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <h1> </h1>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className="flexcol">
                            <h2>A virtual store experience for your customers, online</h2>
                            <p>
                                Showcase your products in interactive 3D with realistic rendering and metal and gemstone effects.
                            </p>
                            <Link to='/demos'>View the demos</Link>
                        </div>
                        <div className='flexcol'>
                            <img className='homepage-section-img'
                                alt='augmented reality for ios and android'
                                src={require('../../assets/images/watch1.jpg')} />
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className="flexcol">
                            <h2>Let customers personalize a product</h2>
                            <p>
                                With the Product Configurator customers can choose from all the precious metal and gemstone combinations before making a purchase.<br/>
                                Customers are more likely to continue through to the checkout if they can personalize a jewelry piece.
                            </p>
                        </div>
                        <div className="flexcol">
                            <div className='homepage-configurator-aspect homepage-viewer'>
                                <div className='aspect-inner '>
                                    <iframe id="ezyvue-viewer"
                                        src={`${process.env.REACT_APP_VIEWER_URL}/viewer?productId=61ba738085c78fd0d85effa5&playbutton&animateplaybutton&hidemenu&showbackground=true`}
                                        width="100%"
                                        height="100%"
                                        style={{ border: 'none' }}
                                        frameBorder="0"
                                        allow="xr-spatial-tracking"
                                        allowFullScreen={true}
                                        mozallowfullscreen="true"
                                        webkitallowfullscreen="true" />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <div className="homepage-section-wrapper" style={{ backgroundColor: '#f8f8f9' }}>
                    <section className='homepage-section'>
                        <h1>Reduce time to market and development costs</h1>
                        <p>
                            With detailed 3d models viewable online, manufacturers can start a marketing campaign sooner and save money.
                            Manufacturers can get customer feedback, make sales forecasts, and reduce costs associated with physical samples.
                            <br/>
                            <br/>
                        </p>
                        <div className="info-boxes-container">
                            <div className='info-box' style={{ backgroundColor: 'rgba(240, 147, 25, 0.3)', fontWeight: 'bold' }}>
                                <div className='aspect-one-one'>
                                    <div className='aspect-inner'>
                                        <img style={{ width: '100%' }} alt='Augmented reality for iOS, Android and WebAR with product personalization' src={require('../../assets/images/ring1.jpg')} />
                                    </div>
                                </div>
                            </div>
                            <div className='info-box' style={{ backgroundColor: 'rgba(240, 147, 25, 0.3)', fontWeight: 'bold' }}>
                                <div className='aspect-one-one'>
                                    <div className='aspect-inner'>
                                        <img style={{ width: '100%' }} alt='Augmented reality for iOS, Android and WebAR with product personalization' src={require('../../assets/images/ring2.jpg')} />
                                    </div>
                                </div>
                            </div> <div className='info-box' style={{ backgroundColor: 'rgba(240, 147, 25, 0.3)', fontWeight: 'bold' }}>
                                <div className='aspect-one-one'>
                                    <div className='aspect-inner'>
                                        <img style={{ width: '100%' }} alt='Augmented reality for iOS, Android and WebAR with product personalization' src={require('../../assets/images/ring3.jpg')} />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="info-boxes-container">
                            <div className='info-box' style={{ backgroundColor: '#DDE3F3', fontWeight: 'bold', height: '200px' }}>
                                        <div className="info-box-outer">
                                            <div className="info-box-inner">
                                                Get feedback before production by sharing viewable digital designs within your company and customers.
                                            </div>
                                </div>
                            </div>
                            <div className='info-box' style={{ backgroundColor: '#DDE3F3', fontWeight: 'bold', height: '200px' }}>
                                        <div className="info-box-outer">
                                            <div className="info-box-inner">
                                                Reduce the number of physical samples that have to be made and sent to customers and reduce waste
                                            </div>
                                </div>
                            </div>
                            <div className='info-box' style={{ backgroundColor: '#DDE3F3', fontWeight: 'bold', height: '200px' }}>
                                        <div className="info-box-outer">
                                            <div className="info-box-inner">
                                                Reduce time to market and development costs
                                            </div>
                                        </div>
                            </div>
                        </div>
                    </section>
                </div>
                <br/>
                <br/>
                <br/>
            </div>
        );
    }
}